@import "src/baseStyles/smart-grid";

.swiper-pagination-bullet {
  background: #FFC907;
}

.swiper-wrapper::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg,  #000 -3.24%, rgba(0, 0, 0, 0.1) 3%, rgba(196, 196, 196, 0) 100%);
  z-index: 9;
  pointer-events: none;
}

.slide__img {
  filter: brightness(40%);
}

.slide {
  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;

    .wrapper {
      height: 100%;
    }

    &--inner {
      display: flex;
      flex-wrap: wrap;
      height: 100%;
    }

    &__text {
      margin-top: 5%;
      max-width: 40%;

      @include md-block() {
        max-width: 60%;
      }

      @include sm-block() {
        max-width: 100%;
      }

      &--title {
        display: inline-block;
        background-color: #E43109;
        margin-bottom: 24px;
        padding: 10px;
        font-size: 24px;
        font-weight: 600;

        @include md-block() {
          margin-bottom: 12px;
          padding: 6px;
          font-size: 18px;
        }

        @include sm-block() {
          background-color: transparent;
        }
      }

      &--desc {
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;

        @include md-block() {
          -webkit-line-clamp: 2;
        }
      }
    }

    &__bottom {
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-top: auto;
      margin-bottom: 30px;

      &--btn {
        align-self: flex-start;
        background-color: #E43109;
        padding: 10px;
        border-radius: 6px;
        font-size: 22px;
        color: #fff;

        @include md-block() {
          font-size: 18px;
        }

        &:hover {
          background-color: #FFC907;
        }
      }

      &--img {
        width: 20%;
        margin-left: auto;
        margin-top: auto;
        border-radius: 8px;
        overflow: hidden;
        box-shadow: 0 0 5px 2px rgba(221, 221, 221, 1);

        @include sm-block() {
          display: none;
        }
      }
    }
  }
}