@import url('https://fonts.googleapis.com/css2?family=Griffy&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');
@import "../../baseStyles/variables";
@import "../../baseStyles/reset";
@import "../../baseStyles/smart-grid";

.wrapper {
  @include wrapper();
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: #FFFFFF;
}

.MuiPagination-root {
  display: flex;
  justify-content: center;
  margin-bottom: 32px;

  .css-yuzg60-MuiButtonBase-root-MuiPaginationItem-root {
    color: #fff;

    @include xxs-block() {
      min-width: 24px;
      width: 24px;
    }

    &:hover {
      color: $color-yellow;
    }
  }
  .css-yuzg60-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected {
    color: $color-yellow;
  }
}
